@media only screen and (max-width: 768px) { 
    .dashboard-page { 
        height: unset !important;
    }

    .dashboard-main-card {
        box-shadow: none !important;
        border: none !important;
    }

    .managable-options {
        justify-content: center !important;
    }
}

.dashboard-page {
    background-color: #e6e7ee;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
}

.dashboard-main-card {
    padding: 4rem;
    text-align: center;
}

.managable-options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.option-container {
    width: 33.3333;
}

.option {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 220px;
    min-width: 220px;
    margin: 2rem;
    cursor: pointer;
}

.option:hover {
    color: #31344b;
    background-color: #e6e7ee;
    border-color: #e6e7ee;
    box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #fff;
}

.option img{
    width: 120px;
    height: 120px;
}